.spinner {
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #52886e;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.fraseContainer {
  background-color: rgba(255, 255, 255, 0.7);
  position: absolute;
  margin: auto;
  width: 63%;
  height: 430px;
  border-radius: 31px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.fraseContainer .texto {
  width: 100%;
  display: flex;
  margin-bottom: 0;
  align-items: center;
}

.fraseContainer .texto span {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 200px;
  padding-bottom: 8%;
  text-align: center;
  margin-left: 5%;
  color: #52886e;
}

.fraseContainer .texto .frase {
  color: #52886e;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 62px;
  text-align: center;
  display: flex;
  justify-content: center;
  margin-right: 5%;
  width: 100%;
}

.fraseContainer .autor {
  width: 100%;
  margin-top: 0;
  padding: 0 10%;
  text-align: end;
  font-style: italic;
  font-family: "PT Sans", sans-serif;
  font-weight: normal;
  font-size: 25px;
  line-height: 47px;
  color: #1f352a;
  display: flex;
  justify-content: flex-end;
}

.fraseContainer .refresh {
  position: absolute;
  left: 95%;
  top: 90%;
  padding: 1% 1.3%;
  border-radius: 50px;
  border: none;
  outline: none;
  background-color: #dcdf33;
}

.fraseContainer .refresh .refreshicon {
  font-size: 50px;
  font-weight: bold;
}

@media screen and (max-width: 1030px) {
  .fraseContainer .texto span {
    font-size: 150px;
  }
  .fraseContainer .texto .frase {
    font-size: 25px;
    line-height: 40px;
  }
  .fraseContainer .autor {
    font-size: 20px;
  }
}

@media screen and (max-width: 690px) {
  .fraseContainer {
    height: 50%;
  }
  .fraseContainer .texto span {
    font-size: 70px;
  }
  .fraseContainer .texto .frase {
    font-size: 20px;
    line-height: 35px;
  }
  .fraseContainer .autor {
    font-size: 15px;
  }
  .fraseContainer .refresh {
    left: 90%;
  }
}

@media screen and (max-width: 570px) {
  .fraseContainer {
    width: 80%;
  }
  .fraseContainer span {
    font-size: 50px;
  }
}

@media screen and (max-width: 490px) {
  .fraseContainer .texto span {
    font-size: 40px;
  }
  .fraseContainer .texto .frase {
    font-size: 17px;
    line-height: 25px;
  }
  .fraseContainer .autor {
    font-size: 14px;
  }
}
